import React from 'react';
import './sidebar.css';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClipboard, faClock, faCamera } from '@fortawesome/free-solid-svg-icons';

function List () {
    return (
        <React.Fragment>
        <li data-tag="1"><FontAwesomeIcon icon={faClipboard} /> Dashboard</li>
        <li data-tag="2"><FontAwesomeIcon icon={faClock} /> View Schedule</li>
        <li data-tag="3"><FontAwesomeIcon icon={faCamera} /> Upload</li>
        </React.Fragment>
    )
}

export default connect((state) => ({

}))(List)